@import url(https://fonts.googleapis.com/css2?family=Cormorant+Upright:wght@300;400;500;600;700&family=Open+Sans:wght@300;400;500;600;700&display=swap);
:root {
  --font-base: 'Cormorant Upright', serif;
  --font-alt: 'Open Sans', sans-serif;
  
  --color-golden: #DCCA87;
  --color-black: #0C0C0C;
  --color-gray: #545454;
  --color-crimson: #F5EFDB;
  --color-grey: #AAAAAA;
  --color-white: #FFFFFF;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

a {
  color: unset;
  text-decoration: none;
}

.slide-bottom {
	animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@keyframes slide-bottom {
  0% {
    transform: translateY(-25%);
  }
  100% {
    transform: translateY(0);
  }
}

.app__chef-content{
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 5rem;
}
.app__chef-content_quote{
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}
.app__chef-content_quote img{
  width: 47px;
  height: 40px;
  margin: 0 1rem 1rem 0;
}
.app__chef-sign{
  width: 100%;
  margin-top: 3rem;
}
.app__chef-sign p:first-child{
  font-family: var(--font-base);
  font-weight: 400;
  font-size: 32px;
  line-height: 41px;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  color:var(--color-golden);
}
.app__chef-sign img{
  width: 250px;
  margin-top: 3rem;
}
@media screen and (max-width:2000px){
  .app__chef-sign img{
      width: 370px;
      margin-top: 3rem;
  }
}
@media screen and (max-width:450px){
  .app__chef-sign img{
      width: 80%;
      margin-top: 3rem;
  }
}
.app__products-wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
    color:var(--color-golden);
}
.app__products-wrapper-top{
    display: flex;
    width: 100%;
    justify-content: space-between;

}
.app__products-wrapper-bottom{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.app__products-wrapper_text{
    flex:1 1;
}
.app__products-wrapper_text h1:hover{
    color:white;
}

.app__products-wrapper_options{
    display: flex;
    justify-content: space-around;
    flex:2 1;
    align-items: center;
   
}
.app__products-wrapper_options >a{
    color:var(--color-golden);
    text-decoration:none;
    font-size:1rem;
    cursor: pointer;

}

.app__products-wrapper_options >a:active{
    border-bottom: 1px solid gray;
}
.sortSection{
    display: flex;
    align-items: center;
}
.sort{
    color:var(--color-golden);
    margin-right: 10px;
    border: none;
    background: transparent;
    font-size: 18px;
    font-family: var(--font-base);
}

.product{
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 10px;
    margin: 5px 10px;
    border: 1px solid black;

}
.product:hover{
    border: 1px solid gray;
    transition: 0.8s;
}
.productTop{
    flex:1 1;
    display: flex;
    align-items: center;
}
.productTop img{
    width: 350px;
    height: 300px;
    border-radius: 10px;
    object-fit: cover;
}
.productBottom{
    flex:1 1;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-top: 10px;
}
.title{
    color:var(--color-golden);
    font-family: var(--font-base);
    font-size: 20px;
    font-weight: 500;
}
.desc{
    font-size: 14px;
    font-weight: 400;
    line-height: 40px;
}
.productDesc{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 10px 0;
}
.star{
    display: flex;
    align-items: center;
    background-color: teal;
    color:white;
    padding-inline: 10px;
    border-radius: 5px;
}
.dash{
    background-color: azure;
    height: 10px;
    width: 1px;
}
.select{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 5px;
    border: none;
    font-size: 16px;
    font-weight: 500;
    background-color: teal;
    color:white;
}
.option{
    padding: 5px !important;
    font-size: 14px;
    align-items: center;
    background-color: white;
    color:teal;
    height: 15px !important;
}
.notchange{
    color:white;

}

.change{
    color: orange;
    transform: scale(1.2);
}

@media screen and (max-width:1150px){
    .product{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .title{
        font-size: 24px;
    }
    .desc{
        font-size: 20px;
    }
}
@media screen and (max-width:650px){
    .productTop img{
        width: 250px;
        height: 200px;
        margin-right: 20px;
    }
    .product{
        display: flex;
        flex-direction: column;
    }

}

